* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    text-decoration: none;
    color: #FEFFFF;
}

html {
    scroll-behavior: smooth;
    background-color: #2B7A7B;
}

section {
    padding: 100px 0;
}

.max-width {
    max-width: 80%;
    padding: 0 80px;
    margin: auto;
}


.about-container .about-container-content,
.projects .serv-content,
.skills .skills-content,
.contact .contact-content {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
}

section .about-title {
    position: relative;
    text-align: center;
    font-size: 40px;
    font-weight: 500;
    margin-bottom: 60px;
    padding-bottom: 20px;
    font-family: "Ubuntu", sans-serif;
}

.navbar {
    background-color: #3AAFA9;
    position: fixed;
    width: 100%;
    z-index: 999;
    padding: 30px 0;
    font-family: "Ubuntu", sans-serif;
    transition: all 0.3s ease;
}

.navbar.sticky {
    padding: 15px 0;
}

.navbar .max-width {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.navbar .name-header a {
    font-size: 35px;
    font-weight: 600;
    color: white;
}


.navbar .menu li {
    list-style: none;
    display: inline-block;
}

.navbar .menu li a {
    display: block;
    color: #fff;
    font-size: 18px;
    font-weight: 500;
    margin-left: 25px;
    transition: color 0.3s ease;
}

.home-container {
    display: flex;
    background-color: #3AAFA9;
    height: 100vh;
    color: #fff;
    min-height: 500px;
    background-size: cover;
    font-family: "Ubuntu", sans-serif;
}

.home-container .max-width {
    width: 100%;
    display: flex;
}

.home-container .max-width .row {
    margin-right: 0;
}

.home-container .container-content .content-name-line {
    font-size: 27px;
}

.about-container .about-container-content .left {
    width: 45%;
}

.about-container .about-container-content .left img {
    height: 400px;
    width: 400px;
    object-fit: cover;
    border-radius: 6px;
}

.about-container .about-container-content .right {
    width: 55%;
}

.about-container .about-container-content .right .text {
    font-size: 25px;
    font-weight: 600;
    margin-bottom: 10px;
}

.about-container .about-container-content .right p {
    size: 18px;
    text-align: justify;
}

.projects,
.teams {
    color: #fff;
    background: #3AAFA9;
}

.projects .serv-content .card {
    width: calc(50% - 30px);
    background: #17252A;
    text-align: center;
    border-radius: 6px;
    padding: 50px 25px;
    cursor: pointer;
    transition: all 0.3s ease;
}

.projects .serv-content .card:hover {
    background: #2B7A7B;
}

.projects .serv-content .card .box {
    transition: all 0.3s ease;
}

.projects .serv-content .card .text {
    font-size: 25px;
    font-weight: 500;
    margin: 10px 0 7px;
}


.skills-container .skills-content .left p {
    text-align: justify;
}

.skills-container .skills-content .right span {
    font-weight: 500;
    font-size: 18px;
}

.skills-container .skills-content .right .line {
    height: 5px;
    width: 100%;
    background: lightgrey;
    position: relative;
}

.skills-container .skills-content .right .line::before {
    content: "";
    position: absolute;
    height: 100%;
    left: 0;
    top: 0;
    background: #17252A;
}

.skills-content .right .html::before {
    width: 80%;
}

.skills-content .right .css::before {
    width: 80%;
}

.skills-content .right .js::before {
    width: 60%;
}

.skills-content .right .php::before {
    width: 70%;
}


footer {
    background: #111;
    padding: 15px 23px;
    color: #fff;
    text-align: center;
}

@media (max-width: 947px) {
    .menu-btn {
        display: block;
        z-index: 999;
    }

    .navbar .menu {
        position: fixed;
        height: 100vh;
        width: 100%;
        left: -100%;
        top: 0;
        text-align: center;
        padding-top: 80px;
        transition: all 0.3s ease;
    }

    .navbar .menu.active {
        left: 0;
    }

    .navbar .menu li {
        display: block;
    }

    .navbar .menu li a {
        display: inline-block;
        margin: 20px 0;
        font-size: 25px;
    }

    .home .container-content .text-2 {
        font-size: 70px;
    }

    .home .container-content .text-3 {
        font-size: 35px;
    }

    .home .container-content a {
        font-size: 23px;
        padding: 10px 30px;
    }

    .max-width {
        max-width: 930px;
    }

    .about-container .about-container-content .column {
        width: 100%;
    }

    .about-container .about-container-content .left {
        display: flex;
        justify-content: center;
        margin: 0 auto 60px;
    }

    .about-container .about-container-content .right {
        flex: 100%;
    }

    .projects .serv-content .card {
        width: calc(50% - 10px);
        margin-bottom: 20px;
    }

    .skills-container .skills-content .column,
    .contact .contact-content .column {
        width: 100%;
        margin-bottom: 35px;
    }
}